/* Reset and base styles  */
* {
    padding: 0px;
    margin: 0px;
    border: none;

    &::after,
    &::before {
        box-sizing: border-box;
        -webkit-box-sizing: border-box;
    }
}

/* Links */

a {
    &:link,
    &:visited {
        // color: inherit;
        text-decoration: none;
        /* display: inline-block; */
    }

    :hover {
        /* color: inherit; */
        text-decoration: none;
    }
}

/* Common */
aside,
nav,
footer,
header,
section,
main {
    display: block;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
    font-size: inherit;
    font-weight: inherit;
}

ul,
ul li {
    list-style-type: none;
}

img {
    vertical-align: top;
}

img,
svg {
    max-width: 100%;
    height: auto;
}

address {
    font-style: normal;
}

/* Form */

input,
textarea,
button,
select {
    font-family: inherit;
    font-size: inherit;
    color: inherit;
    background-color: transparent;
}

select {
    outline: none;
}

input::-ms-clear {
    display: none;
}

button,
input[type='submit'] {
    display: inline-block;
    box-shadow: none;
    background-color: transparent;
    background: none;
    cursor: pointer;
}

input:focus,
input:active,
button:focus,
button:active,
textarea:focus {
    outline: none;
}

button::-moz-focus-inner {
    padding: 0;
    border: 0;
}

button::-moz-focus-inner {
    border: 0;
}

label {
    cursor: pointer;
}

legend {
    display: block;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}
